import React, { Component } from 'react';
import PropTypes from 'prop-types';
import DetailsPageTemplate from 'templates/DetailsPageTemplate';
import { routes } from 'routes';
import malezycie from 'assets/images/malezycie.jpg';

class DetailsPage extends Component {
  state = {
    pageType: 'books',
  };

  componentDidMount() {
    const { match } = this.props;

    switch (match.path) {
      case routes.book:
        this.setState({ pageType: 'books' });
        break;

      case routes.author:
        this.setState({ pageType: 'authors' });
        break;

      case routes.note:
        this.setState({ pageType: 'notes' });
        break;

      default:
        console.log('Something went wrong with matching paths');
    }
  }

  render() {
    const exampleNote = {
      id: '10',
      title: 'Małe życie',
      author: 'Hanya Yanagihara',
      content:
        'Zachwycająca powieść o przyjaźni poddanej ogromnej próbie. Oto historia życia czterech przyjaciół – o ich niełatwym dojrzewaniu, ogromnych sukcesach, bolesnych wyborach i tyranii pamięci, od której nie sposób czasem się uwolnić… To również opowieść o jednym z najbardziej fascynujących miast świata – Nowym Jorku – który jest piątym, równorzędnym bohaterem tej olśniewającej książki.',
      imageUrl: malezycie,
    };

    const { pageType } = this.state;

    return (
      <DetailsPageTemplate
        pageType={pageType}
        title={exampleNote.title}
        author={exampleNote.author}
        content={exampleNote.content}
        imageUrl={exampleNote.imageUrl}
      />
    );
  }
}

DetailsPage.propTypes = {
  match: PropTypes.shape({
    path: PropTypes.string.isRequired,
  }).isRequired,
};

export default DetailsPage;
